.App {
  font-family: 'Arial', sans-serif;
  height: 100vh;
  margin: 0;
}

.background-image {
  background-image: url(https://img.freepik.com/fotos-gratis/vista-aerea-de-uma-equipe-de-negocios_53876-124515.jpg);
  background-size: cover;
  background-position: center;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.container {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  padding: 30px;
  width: 500px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #333;
  font-size: 2em;
  margin-bottom: 20px;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.form input {
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form button {
  padding: 10px;
  background-color: #493737;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.form button:hover {
  background-color: #a88484;
}

.employee-list {
  margin-top: 30px;
}

.employee-card {
  background-color: #f9f9f9;
  border-radius: 10px;
  padding: 15px;
  margin-bottom: 15px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.employee-card h3 {
  font-size: 1.2em;
  color: #967a7a;
}

.info {
  font-size: 1em;
  color: #523131;
}

.info p {
  margin: 5px 0;
}

strong {
  color: #6e2323;
}

.remove-btn {
  padding: 8px 12px;
  background-color: #a07c79;
  color: rgb(27, 1, 1);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.remove-btn:hover {
  background-color: #300908;
}

/* LGPD Section */
.lgpd-section {
  margin-top: 30px;
  text-align: center;
}

.observacao {
  font-size: 16px;
  font-weight: bold;
  color: #021529;
  margin-bottom: 15px;
}

.lgpd-btn {
  padding: 10px 20px;
  background-color: #03131d;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

.lgpd-btn:hover {
  background-color: #031724;
}

.lgpd-info {
  margin-top: 15px;
  font-size: 16px;
  line-height: 1.6;
  background-color: #ecf0f1;
  padding: 15px;
  border-radius: 5px;
  border: 1px solid #bdc3c7;
}
